import React from 'react'
import styles from './styles.module.css'

import SettingsIcon from './icons/Settings'
import DoneIcon from './icons/Done'

import Loading from './Loading'

const Source = ({ linked = false, icon, label, tenant, flow, tap, setOpening, styleOverrides }) => {
  const hotglue = window && window.HotGlue

  const onClickSource = (e) => {
    e.preventDefault();
    setOpening(true);

    if (!linked) {
      hotglue.link(tenant, flow, tap);
    }
  }

  return (
    <div className={styles['hg-elements-source']} onClick={onClickSource} style={Object.assign(
      {cursor: linked ? "default" : "pointer"},
      linked ? styleOverrides.linkedSource : styleOverrides.unlinkedSource
    )}>
      <div className={styles['hg-elements-imageContainer']} style={styleOverrides.imageContainer}>
        <img src={icon} />
      </div>
      <div>
        <p>{label}</p>
        {linked ? (
          <div className={styles['hg-elements-connected']} style={styleOverrides.connected}>
            <span>Connected</span>
            <DoneIcon styleOverrides={styleOverrides} />
          </div>
        ) : null}
      </div>
      {linked && (
        <button onClick={() => hotglue.link(tenant, flow, tap)}>
          <SettingsIcon styleOverrides={styleOverrides} />
        </button>
      )}
    </div>
  )
}

const Flow = ({ name, description, sources, styleOverrides }) => {
  return (
    <div className={`${styles['hg-elements-flow']} ${styles['hg-elements-unlinkedFlow']}`} style={styleOverrides.unlinkedFlow}>
      <h4>{name} data</h4>
      <p>{description}</p>
      <div>{sources}</div>
    </div>
  )
}

const LinkedFlow = ({ name, description, icon, label, tenant, flow, tap, setOpening, styleOverrides }) => {
  return (
    <div className={`${styles['hg-elements-flow']} ${styles['hg-elements-linkedFlow']}`} style={styleOverrides.linkedFlow}>
      <h4>{name} data</h4>
      <p>{description}</p>
      <div>
        <Source
          linked
          icon={icon}
          label={label}
          tap={tap}
          tenant={tenant}
          flow={flow}
          setOpening={setOpening}
          styleOverrides={styleOverrides}
        />
      </div>
    </div>
  )
}

const Connections = ({ tenant, onLink, onUnlink, onWidgetClose, onPopupClose, loadingComponent, styleOverrides = {} }) => {
  const [supportedFlows, setSupportedFlows] = React.useState()
  const [supportedSources, setSupportedSources] = React.useState({})

  const [linkedFlows, setLinkedFlows] = React.useState()
  const [linkedSources, setLinkedSources] = React.useState({})

  const [isLoading, setLoading] = React.useState(true);
  const [isOpening, setOpening] = React.useState(false);
  const [isRefreshing, setRefreshing] = React.useState(false);
  const [listenerMounted, setListenerMounted] = React.useState(false);

  const hotglue = window && window.HotGlue

  // Helper function to make loading state management more readable.
  const loadingCompleted = (_) => {
    // Exit early if loading has already completed.
    if (!isLoading || isRefreshing) return false

    if (
      supportedFlows &&
      linkedFlows &&
      Object.keys(supportedSources).length === supportedFlows.length &&
      Object.keys(linkedSources).length === linkedFlows.length
    ) {
      return true
    }
  }

  // See what sources are linked and link them
  const getLinkedSources = async () => {
    const linked = {}

    // See what sources are linked
    for (const lf of linkedFlows) {
      const flow = lf.id

      const ls = await hotglue.getLinkedSources(flow, tenant);
      linked[flow] = ls[0]
    }

    setLinkedSources(linked)
  }

  // Helper function to fetch linked flows
  const fetchLinkedFlows = (_) => {
    if (linkedFlows) return

    if (hotglue && hotglue.hasMounted()) {
      hotglue.getLinkedFlows(tenant).then((_linkedFlows) => {
        setLinkedFlows(_linkedFlows)
      })
    } else {
      setTimeout(() => fetchLinkedFlows(), 1000);
    }
  }

  const refreshData = () => {
    setLinkedFlows(undefined);
    setLinkedSources({});
    setSupportedFlows(undefined);
    setSupportedSources({});
    setRefreshing(false);
  };

  const triggerRefresh = () => {
    setRefreshing(true);
    setLoading(true);
  }

  // Mark loading complete once loading has finished
  React.useEffect((_) => {
    if (loadingCompleted()) {
      setLoading(false)
    }

    if (hotglue && hotglue.hasMounted() && !listenerMounted) {
      setListenerMounted(true);

      hotglue.setListener({
        onPopupClose: (id, flowId) => {
          // Set opening to false
          setOpening(false);
          // Forward to listener, if set
          onPopupClose && onPopupClose(id, flowId);
        },
        onWidgetClose: () => {
          // Set opening to false
          setOpening(false);
          // Forward to listener, if set
          onWidgetClose && onWidgetClose();
        },
        onSourceLinked: (source, flowId) => {
          // Refresh data
          triggerRefresh();
          // Forward to listener, if set
          onLink && onLink(source, flowId);
        },
        onSourceUnlinked: (source, flowId) => {
          // Refresh data
          triggerRefresh();
          // Forward to listener, if set
          onUnlink && onUnlink(source, flowId);
        }
      });
    }
  })

  React.useEffect(() => {
    if (isLoading && isRefreshing) {
      // Once refresh is triggered, fetch the data
      refreshData();
    }
  }, [isLoading, isRefreshing]);

  // On update of linkedFlows
  React.useEffect(
    (_) => {
      // If we've fetched the linked flows, get the linked sources
      if (
        linkedFlows &&
        Object.keys(linkedSources).length !== linkedFlows.length
      ) {
        getLinkedSources()
      }

      if (!linkedFlows) {
        // Fetch linked flows
        fetchLinkedFlows()
      }
    },
    [linkedFlows]
  )

  const getSupportedSources = async (supportedFlows) => {
    const supported = {}

    // See what sources are linked
    for (const sf of supportedFlows) {
      const flow = sf.id

      const ss = await hotglue.getSupportedSources(flow)
      supported[flow] = ss || []
    }

    setSupportedSources(supported)
  }

  const getSupportedFlows = () => {
    // If we haven't fetched supported flows already, fetch and set
    if (!supportedFlows) {
      if (hotglue && hotglue.hasMounted()) {
        // Fetch supported flows
        hotglue.getSupportedFlows().then((sf) => {
          setSupportedFlows(sf)
        })
      } else {
        setTimeout(() => getSupportedFlows(), 1000);
      }
    }
  }

  // On update of supportedFlows
  React.useEffect(
    (_) => {
      // If we've already fetched the supported flows, get the supported sources
      if (
        supportedFlows &&
        Object.keys(supportedSources).length !== supportedFlows.length
      ) {
        getSupportedSources(supportedFlows)
      }

      getSupportedFlows();
    },
    [supportedFlows]
  )

  if (isLoading || isOpening) {
    return (
      <div className={styles['hg-elements-loadingContainer']} style={styleOverrides.loadingContainer}>
        {loadingComponent || <Loading />}
      </div>
    )
  }

  const createFlow = (supportedFlow) => {
    const { id: flow, name, description } = supportedFlow
    const sources = supportedSources[flow].map(({ icon, tap, label }) => (
      <Source icon={icon} label={label} key={tap} tap={tap} tenant={tenant} flow={flow} setOpening={setOpening} styleOverrides={styleOverrides} />
    ))

    return {
      flow,
      name,
      description,
      sources
    }
  }

  const createLinkedFlow = (linkedFlow) => {
    const { id: flow, name, description } = linkedFlow
    const { label, icon, tap } = linkedSources[flow]

    return {
      isLinked: true,
      flow,
      name,
      description,
      label,
      icon,
      tap
    }
  }

  const flows = supportedFlows.filter(sf => !sf.type).map((supportedFlow) => {
    const linkedFlow = linkedFlows.find((flow) => flow.id === supportedFlow.id)

    if (linkedFlow) {
      return createLinkedFlow(linkedFlow)
    }

    return createFlow(supportedFlow)
  })

  // Sort flows by name alphabetically
  flows.sort((a, b) => {
    if (a.name < b.name) return -1
    if (a.name > b.name) return 1
    return 0
  })

  const flowElements = flows.map((_flow) => {
    if (_flow.isLinked) {
      const { name, description, icon, label, flow, tap } = _flow

      return (
        <LinkedFlow
          name={name}
          description={description}
          icon={icon}
          label={label}
          tenant={tenant}
          flow={flow}
          tap={tap}
          key={flow}
          setOpening={setOpening}
          styleOverrides={styleOverrides}
        />
      )
    }

    const { flow, name, description, sources } = _flow
    return (
      <Flow
        name={name}
        description={description}
        sources={sources}
        key={flow}
        styleOverrides={styleOverrides}
      />
    )
  })

  return <div className={styles['hg-elements-flowContainer']} style={styleOverrides.flowContainer}>{flowElements}</div>
}

export default Connections
